import React from 'react';
import moment from 'moment';
import axios from 'axios';
import './style/form.css';

export default class Form extends React.Component {
    state = {
        name: "", //  
        phone: "", //  
        countryCode: this.props.countryCode, //  
        createdAt: "", 
        streamCode: "dbql", //  , !!!     ,       1 
        offerId: "1311", // id      !!!     ,        
        landingUrl: "url", //   url
        ip: this.props.ipUser, // ip  
        userAgent: window.navigator.userAgent, //   
        sub1: window.location.search.length ? window.location.search.split("?")[1].split("=")[1] : "",
        loading: false,
        send: false
    }

    submitForm = (e) => {
        e.preventDefault();
        this.setState({ createdAt: moment().format("YYYY-MM-DD HH:mm:ss"), loading: true }, () => {
            axios({
                method: 'post',
                url: "https://life-changer.ru/api/success.php",
                header: {
                    'Access-Control-Allow-Origin': "*"
                },
                data:  JSON.stringify({ ...this.state })
            }).then(res => {
                this.setState({ loading: false, send: true }, () => {
                    setTimeout(() => {
                        this.setState({ send: false })
                    }, 400000);
                });
            }).catch(err => {
                this.setState({ loading: false, send: true }, () => {
                    setTimeout(() => {
                        this.setState({ send: false })
                    }, 400000);
                });
            })
        })
    }

    onChangeInput = (e) => {
        this.setState({ [e.target.name]: e.target.value });
    }

    render(){
        return(
            <form className="form-box" onSubmit={this.submitForm}>
                {
                    this.state.send ?    
                    <p>!    !</p>
                    :
                    <React.Fragment>
                        <label>
                            <input onChange={this.onChangeInput} type="text" name="name" placeholder="  " required />
                        </label>
                        <label>
                            <input pattern="([\d\s\-\.\(\)\+]{7,25})" minLength="7" onChange={this.onChangeInput} type="text" name="phone" placeholder=" " required />
                        </label>
                        <button className="btn" disabled={this.state.loading ? "disable" : ''}>
                           {this.state.loading ? "..." : ""} 
                        </button>
                    </React.Fragment>
                }

            </form>
        )
    }
}


